import faqContent from "@/components/faqContent";
export default {
  async asyncData({ $axios, i18n, params }) {
    params.lang = i18n.locale == "en" ? "en" : i18n.locale;
    const { data } = (await $axios.get(`to-invest`, { params })).data;

    return { data };
  },
  components: {
    faqContent,
  },
};
